import React, { useState } from 'react'
import { NavLink, useRouteMatch } from 'react-router-dom'
import { MdHome, MdLocalOffer, MdPerson, MdSettings } from "react-icons/md"
import { FaTimes } from "react-icons/fa"

export default function NavBar() {
    const [isOpen, setOpen] = useState(false)
    const [path, setpath] = useState(useRouteMatch())


    return (
        <><div className="nav-icon" onClick={() => setOpen(true)}>
            <span></span>
            <span></span>
            <span></span>
        </div>
            <header className={isOpen ? "open" : null}>

                <h1 className="logo">GREEN'ET</h1>
                <div className="exit-icon" onClick={() => setOpen(false)}>
                    <FaTimes />
                </div>
                <nav>
                    <ul>
                        <NavLink to='/user'><li className={path.path === "/user" ? "active" : null}><span><MdHome /></span>overview</li></NavLink>
                        <NavLink to="/services"><li className={path.path === "/services" ? "active" : null}><span><MdLocalOffer /></span>services</li></NavLink>
                        <NavLink to="#services"><li>Lands</li></NavLink>
                        <NavLink to="#services"><li>seeds</li></NavLink>
                        <NavLink to="#services"><li>contact</li></NavLink>
                    </ul>
                    <ul>
                        <NavLink to="/login"><li><span><MdSettings /></span>setting</li></NavLink>
                        <NavLink to="/"><li> <span><MdPerson /></span>logout</li></NavLink>
                    </ul>
                </nav>
            </header>
        </>
    )
}
