import React from 'react'
import { MdAdd, MdCheck, MdNotificationsNone } from 'react-icons/md';
import { FaToggleOn } from "react-icons/fa"
import NavBar from '../components/navBar/NavBar';
import History from "../components/History"
import CurrentPlant from '../components/CurrentPlant';
import UserActivity from '../components/UserActivity';

export default function Services() {
    return (<>
        <NavBar />
        <main className="user services">
            <h1 className="logo">GREEN'ET</h1>
            <section className="land-info">
                <div className="header">
                    <div className="warpper">
                        <h1>active services</h1>
                    </div>
                    <div className="warpper">
                        <div className="notification">
                            <MdNotificationsNone />
                        </div>
                    </div>

                </div>
                <section className="services lands">
                    <div className="card active-card">
                        <div className="row"><h3>production managment</h3>
                            <div className="toggle"><FaToggleOn /></div></div>
                        <div className="container">
                            <div className="row">
                                <p><span><MdCheck /></span>managing your land production with experts</p>

                            </div>

                        </div>
                    </div>
                    <div className="card">
                        <div className="col">
                            <div className="add-icon">
                                <MdAdd />
                            </div>
                            <p>add a new service</p>
                        </div>
                    </div>
                </section>
            </section>
            <section className="land-info">
                <div className="header">
                    <div className="warpper">
                        <h1>Product</h1>
                    </div>
                </div>
                <section className="product lands">
                    <div className="card">
                        <div className="col">
                            <h2>seeds</h2>
                            <button className="active">shop</button>
                        </div>
                    </div>
                    <div className="card">
                        <div className="col">
                            <h2>Fertilizer</h2>
                            <button className="active">shop</button>
                        </div>
                    </div>
                </section>
            </section>
            <div className="col-2" >
                <div className="colo" style={{ width: "50%" }}>
                    <section className="history">
                        <div className="header">
                            <h1>Product</h1>
                            <button className="action">
                                view more
                    </button>
                        </div>
                        <div className="body">
                            <ul>
                                <div className="row">
                                    <div className="activity">
                                        <h3>Tomato</h3>

                                    </div>
                                    <button className="action">buy</button>
                                </div>
                                <div className="row">
                                    <div className="activity">
                                        <h3>potasum</h3>
                                    </div>
                                    <button className="action">buy</button>

                                </div>
                                <div className="row">
                                    <div className="activity">
                                        <h3>potato</h3>
                                    </div>
                                    <button className="action">buy</button>

                                </div>
                            </ul>
                        </div>
                    </section>
                </div>
                <div className="colo" style={{ width: "50%" }}><History /></div>
            </div>
        </main>
    </>
    )
}
